import { useCallback } from 'react'

import CascadingMenuButton from '@jbrowse/core/ui/CascadingMenuButton'
import { getSession } from '@jbrowse/core/util'
import { makeStyles } from '@jbrowse/core/util/tss-react'
import MoreHorizIcon from '@mui/icons-material/MoreHoriz'
import FilledStarIcon from '@mui/icons-material/Star'
import StarIcon from '@mui/icons-material/StarBorderOutlined'

import type { HierarchicalTrackSelectorModel } from '../../model.ts'
import type { AnyConfigurationModel } from '@jbrowse/core/configuration'

const useStyles = makeStyles()({
  cascadingStyle: {
    padding: 0,
  },
})

const TrackSelectorTrackMenu = function ({
  id,
  stopPropagation,
  model,
  setOpen,
  conf,
}: {
  id: string
  stopPropagation?: boolean
  conf: AnyConfigurationModel
  setOpen?: (arg: boolean) => void
  model: HierarchicalTrackSelectorModel
}) {
  const { classes } = useStyles()
  const trackId = conf.trackId as string

  const getMenuItems = useCallback(() => {
    const session = getSession(model)
    const flatMenuItems = session.getTrackListMenuItems?.(conf) || []
    return [
      ...flatMenuItems,
      model.isFavorite(trackId)
        ? {
            label: 'Remove from favorites',
            onClick: () => {
              model.removeFromFavorites(trackId)
            },
            icon: StarIcon,
          }
        : {
            label: 'Add to favorites',
            onClick: () => {
              model.addToFavorites(trackId)
            },
            icon: FilledStarIcon,
          },
      {
        label: 'Add to selection',
        onClick: () => {
          model.addToSelection([conf])
        },
      },
      ...(model.isSelected(conf)
        ? [
            {
              label: 'Remove from selection',
              onClick: () => {
                model.removeFromSelection([conf])
              },
            },
          ]
        : []),
    ]
  }, [conf, model, trackId])

  return (
    <CascadingMenuButton
      className={classes.cascadingStyle}
      stopPropagation={stopPropagation}
      setOpen={setOpen}
      data-testid={`htsTrackEntryMenu-${id}`}
      menuItems={getMenuItems}
    >
      <MoreHorizIcon />
    </CascadingMenuButton>
  )
}

export default TrackSelectorTrackMenu
